.container {
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  inset: 0;
}

.page {
  background-color: #ccc;
  height: 100vh;
  margin: 0;
  padding: 0;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  margin: 0;
  position: relative;
}

.stopwatch__middle {
  z-index: 2;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 37.5vmin;
  left: 37.5vmin;
}

.stopwatch__big-pointer {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite sec-rotation;
  position: absolute;
  bottom: 40vmin;
  left: 39.25vmin;
}

.stopwatch__small-pointer {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s step-end infinite min-rotation;
  position: absolute;
  bottom: 40vmin;
  left: 38.5vmin;
}

.stopwatch--speed-up :nth-child(2) {
  animation: 600s step-end infinite min-rotation;
}

.stopwatch--speed-up :nth-child(3) {
  animation: 10s linear infinite sec-rotation;
}

